* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: white;
  overflow-x: hidden;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 16px !important;
  position: relative;
}

.main-section, .index-doc-div {
  min-height: calc(100vh - 360px + 80px);
  width: 100%;
}

.top-padding-page-section {
  padding-top: 2em !important;
}

.boutique-section {
  position: relative;
  min-height: calc(100vh - 120px - 200px);
  width: 100%;
}

.bg-light {
  background-color: #f5f5f5;
}

.bg-dark {
  background-color: #0a0909;
}

.bg-main {
  background-color: #2371d7;
}

.bg-secondary {
  background-color: #58E6D9;
}

.text-light {
  color: #f5f5f5;
}

.hidden {
  display: none;
}

.visible {
  display: block !important;
}

.flex-visible {
  display: flex !important;
}

.relative {
  position: relative;
  z-index: 50;
}

.noscroll {
  overflow: hidden;
}

@media (min-width: 960px) {
  .container {
    padding: 0 0px;
  }
}
@media (min-width: 530px) {
  .main-section, .index-doc-div {
    min-height: calc(100vh - 360px + 100px);
    width: 100%;
  }
}
.mt-25 {
  margin-top: 25px;
}

.two-thirds {
  width: 66.66%;
}

.one-half {
  width: 50%;
}

.one-third {
  width: 33.33%;
}

header {
  background-color: #f5f5f5;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  padding: 0 24px;
  position: relative;
  border-bottom: 2px solid #0a0909;
}

.toggle-hamburger {
  position: absolute;
  right: 16px;
  display: none;
  cursor: pointer;
  font-size: 1.8em;
  display: flex;
  z-index: 502;
  color: #0a0909;
}

.fa-window-close {
  color: #f5f5f5;
}

.nav {
  display: relative;
}
.nav-desktop {
  display: none;
}
.nav-mobile {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 501;
  background-color: #0a0909;
  opacity: 0.9;
  min-width: 70vw;
  justify-content: center;
  align-items: center;
  padding: 50px 50px;
  border-radius: 20px;
  border: 1px solid #DE147B;
  -webkit-box-shadow: inset 0px 0px 9px 2px #f5f5f5;
  box-shadow: inset 0px 0px 9px 2px #f5f5f5;
}
.nav-mobile ul {
  display: flex;
  flex-direction: column;
  gap: 35px;
  text-align: center;
  color: #f5f5f5;
}
.nav-mobile ul li a {
  font-size: 1.3em;
  font-weight: 600;
  letter-spacing: 1.8px;
}
.nav-mobile ul li a:hover {
  color: #DE147B;
}

.overlay {
  position: fixed;
  overflow-y: hidden;
  overscroll-behavior: contain;
  inset: 0;
  z-index: 101;
  background: #0a0909;
  opacity: 0.75;
  display: none;
}

@media (min-width: 1150px) {
  header .toggle-hamburger {
    position: absolute;
    right: 16px;
    display: none;
    cursor: pointer;
    font-size: 2em;
    z-index: 502;
  }
  .nav-desktop {
    display: flex;
  }
  .nav-desktop ul {
    display: flex;
    gap: 3em;
  }
  .nav-desktop ul li a {
    color: #0a0909;
    font-size: 1.15em;
    font-weight: 600;
    padding: 8px 16px;
    border-radius: 35px;
    cursor: pointer;
    transition: all 0.3s ease-out;
    letter-spacing: 1.8px;
  }
  .nav-desktop ul li a:hover {
    color: #f5f5f5;
    background-color: #0a0909;
  }
  .nav-desktop ul li a.active {
    color: #f5f5f5;
    background-color: #0a0909;
  }
  .nav-mobile {
    display: none;
  }
}
.site-footer {
  padding: 2rem 0 3.5rem 0;
  background-color: #f5f5f5;
  height: 400px;
}
.site-footer p {
  font-size: 0.9rem;
}
.site-footer .headline {
  color: #DE147B;
  opacity: 0.8;
  margin-bottom: 0.33rem;
}
.site-footer__link {
  color: #999;
  text-decoration: none;
}
.site-footer__link:hover {
  color: #2371d7;
  text-decoration: underline;
}
.site-footer__inner {
  border-top: 2px dotted #0a0909;
  padding-top: 3.5rem !important;
}
.site-footer__col-one {
  text-align: center;
}
@media (min-width: 960px) {
  .site-footer__col-one {
    text-align: left;
    width: 33%;
    float: left;
  }
}
.site-footer__col-two-three-group {
  text-align: center;
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .site-footer__col-two-three-group {
    text-align: left;
    float: left;
    width: 42%;
    margin: 0;
  }
}
.site-footer__col-two {
  width: 50%;
  float: left;
}
.site-footer__col-three {
  width: 50%;
  float: left;
}
.site-footer__col-four {
  padding-top: 20px;
  width: 85%;
  margin: 0 auto;
  clear: both;
  text-align: center;
}
@media (min-width: 960px) {
  .site-footer__col-four {
    padding-top: 0;
    margin: 0;
    clear: none;
    text-align: left;
    width: 25%;
    float: right;
  }
}

.group:after {
  clear: both;
}

.group:before,
.group:after {
  content: " ";
  display: table;
}

.headline {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
}

.headline--large {
