  margin-bottom: 1.9rem;
}

.headline--small {
  font-size: 1.2rem;
  margin-bottom: 1.9rem;
}

@media (min-width: 530px) {
  .headline--small {
    font-size: 1.6875rem;
  }
}
.headline--smaller {
  font-size: 1.5rem;
}

.headline--tiny {
