  font-size: 1.3875rem;
  margin-bottom: 0.25rem;
}

.nav-list ul {
  padding: 0;
  margin: 0;
}

.nav-list li {
  list-style: none;
}

.nav-list a {
  font-size: 0.9rem;
  color: rgb(109, 107, 107);
  text-decoration: none;
}

.nav-list a:hover {
  color: #2371d7;
  text-decoration: underline;
}

.min-list {
  padding: 0;
  margin: 0;
}

.min-list li {
  list-style: none;
}

.social-icons-list {
