}

.title-footer {
  color: #0a0909;
  font-size: 2.1em;
  line-height: 1;
  margin-bottom: 1.2em;
  text-shadow: 1px 2px 5px #DE147B;
}

@media (min-width: 530px) {
  .site-footer {
    height: 400px;
  }
}
@media (min-width: 767px) {
  .site-footer {
    height: 400px;
  }
}
@media (min-width: 1150px) {
  .site-footer {
    height: 300px;
  }
}
.search-overlay {
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 50000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #0a0909;
  visibility: hidden;
  opacity: 0;
  transform: scale(1.09);
  transition: opacity 0.3s, transform 0.3s, visibility 0.3s;
  box-sizing: border-box;
}
.search-overlay p {
  padding-top: 15px;
}
.search-overlay .event-summary p {
  padding-top: 0;
}
.search-overlay .event-summary {
  margin-bottom: 0;
  margin-top: 20px;
}
body.admin-bar .search-overlay {
  top: 32px;
}
.search-overlay__top {
  background-color: rgba(0, 0, 0, 0.12);
}
.search-overlay__icon {
  margin-right: 10px;
  font-size: 1.8rem;
  color: rgba(245, 162, 8, 0.966);
}
.search-overlay--active {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.search-overlay__section-title {
  margin: 30px 10px 1px 0;
  font-weight: 400;
  color: #0a0909;
  font-size: 2rem;
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}
.search-overlay__close {
  position: absolute;
  top: 5px;
  right: 16px;
  font-size: 2.1rem;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #fff;
  color: rgba(245, 162, 8, 0.966);
  line-height: 0.7;
}
.search-overlay__close:hover {
  opacity: 1;
}
.search-overlay .one-half {
  padding-bottom: 0;
}

.search-overlay__section-title {
  margin: 30px 0 1px 0;
  font-weight: 400;
  color: #0D3B66;
  font-size: 2rem;
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}

.search-term {
  width: 75%;
  box-sizing: border-box;
  border: none;
  padding: 15px 0;
  margin: 0;
  background-color: transparent;
  font-size: 1rem;
  font-weight: 300;
  outline: none;
  color: aliceblue;
}

.body-no-scroll {
  overflow: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.spinner-loader {
  margin-top: 45px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 0.25rem solid rgba(0, 0, 0, 0.2);
  border-top-color: black;
  -webkit-animation: spin 1s infinite linear;
  animation: spin 1s infinite linear;
}

.min-list {
  padding: 0;
  margin: 0;
}

.min-list li {
  list-style: none;
}

.link-list li {
  padding: 1rem 0;
  font-size: 1.5rem;
  border-bottom: 1px dotted #DEDEDE;
}

.search-overlay .link-list li {
  font-size: 1.3rem;
}

.link-list li:last-child {
  border-bottom: none;
}

.link-list a {
  display: flex;
  align-items: flex-end;
  gap: 5px;
  color: aliceblue;
}

.search-form-row {
  display: flex;
}

@media (min-width: 960px) {
  .row {
    margin-right: -50px;
  }
  .one-half,
  .one-third,
  .two-thirds,
  .one-fourth,
  .one-fifth,
  .one-sixth {
    padding-bottom: 0;
    float: left;
    box-sizing: border-box;
    padding-right: 50px;
  }
  .two-thirds {
    width: 66.66%;
  }
  .one-half {
