  }
}
/* Seach Form */
.search-form {
  background-color: #2371d7;
  padding: 20px;
  margin-top: 60px;
  text-align: center;
}

.search-form label {
  display: block;
}

.search-form-row {
  display: flex;
  margin-top: 25px;
  justify-content: center;
}

.search-form #s {
  min-width: 20px;
  flex-basis: auto;
  flex-grow: 1;
  border: none;
  font-family: "Roboto", sans-serif;
  padding: 10px;
}

@media (min-width: 530px) {
  .search-form #s {
    font-size: 1.4rem;
  }
}
.search-submit {
  color: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
  background-color: #0a0909;
  padding: 0 20px;
  margin-left: 10px;
}

.index-doc-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.index-list-grid-structure {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.index-list-grid-detail {
  position: relative;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 3fr;
  grid-column-gap: 20px;
  grid-row-gap: 2.2em;
  border-radius: 25px;
}
.index-list-grid-detail article {
  cursor: pointer;
  display: flex;
  z-index: 12;
  justify-content: center;
  position: relative;
  width: 100%;
  border-radius: 25px;
}
.index-list-grid-detail article .content-card-doc {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 25px;
  width: 100%;
  overflow: hidden;
  position: relative;
  z-index: 1;
  border: 1px solid #0a0909;
}
.index-list-grid-detail article .content-card-doc .title {
  position: absolute;
  bottom: 0;
  left: 0%;
  right: 0%;
  text-align: center;
  padding: 12px 0;
  font-size: 0.8em;
  opacity: 0.8;
}
.index-list-grid-detail article .content-card-doc .title-1 {
  background-color: #0a0909;
  color: #f5f5f5;
}
.index-list-grid-detail article .content-card-doc .title-2 {
  background-color: #0a0909;
  color: #f5f5f5;
}
.index-list-grid-detail article .content-card-doc .title-3 {
  background-color: #0a0909;
  color: #f5f5f5;
}
.index-list-grid-detail article .content-card-doc .doc-img {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  transition: all 0.2s ease-in;
}
.index-list-grid-detail article .content-card-doc .doc-img:hover {
  transform: scale(1.1);
}
.index-list-grid-detail article .debord-1 {
  position: absolute;
  top: 1%;
  left: 1%;
  background-color: #2371d7;
  height: 102%;
  width: 101%;
  z-index: 0;
  border-radius: 25px;
}
.index-list-grid-detail article .debord-2 {
  position: absolute;
  top: 1%;
  left: 1%;
  background-color: #96c03d;
  height: 102%;
  width: 101%;
  z-index: 0;
  border-radius: 25px;
}
.index-list-grid-detail article .debord-3 {
  position: absolute;
  top: 1%;
  left: 1%;
  background-color: #DE147B;
  height: 102%;
  width: 101%;
  z-index: 0;
  border-radius: 25px;
}

@media (min-width: 767px) {
  .index-list-grid-detail {
    padding: 4em 2em;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 24px;
    grid-row-gap: 2em;
  }
}
.list-of-related-page-category {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 8px;
}
.list-of-related-page-category .the-cat-name {
  margin-bottom: 8px;
  font-weight: 500;
}
.list-of-related-page-category .the-cat-name a {
  font-size: 22px;
  font-weight: 600;
}
.list-of-related-page-category a {
  font-size: 14px;
  transition: all 0.2s ease-in;
}
.list-of-related-page-category a h3 {
  font-weight: 500;
}
.list-of-related-page-category a:hover {
  color: #2371d7;
}
.list-of-related-page-category .active-link {
  color: #2371d7;
}

