.button_single_variation_div_relative {
  margin-top: 2em;
  position: relative;
  display: inline-block;
  justify-content: center;
  align-items: center;
}

.single_add_to_cart_button {
  width: 250px;
  background-color: #DE147B !important;
  position: relative;
  z-index: 3 !important;
  border-radius: 25px !important;
  color: #f5f5f5 !important;
  height: 60px;
  transition: all 0.3s ease-in;
  border: 1px solid #f5f5f5 !important;
}
.single_add_to_cart_button + .debord {
  position: absolute;
  z-index: 2 !important;
  top: 2%;
  left: 2%;
  background-color: #0a0909;
  width: 102%;
  height: 110%;
  border-radius: 30px !important;
  transition: all 0.3s ease-in;
}
.single_add_to_cart_button:hover {
  background-color: #0a0909 !important;
  color: #DE147B;
}
.single_add_to_cart_button:hover + .debord {
  background-color: #DE147B;
}

.disabled:hover {
  background-color: #a46497 !important;
}
.disabled + .debord {
  display: none;
}

.woocommerce div.product form.cart .variations {
